@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&display=swap);
*{box-sizing:border-box}html{background-color:#003844;color:#ffebc6;overflow-x:hidden}body{padding:12vmin 0;margin:0;font-family:'Roboto Mono', monospace, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body h2{font-family:'DotGothic16', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}p{margin:0 0 10px;font-size:0.9rem}.div_about,.div_experiences,.div_projects{position:absolute;width:85%;float:left;z-index:1}@media (min-width: 500px){.div_about,.div_experiences,.div_projects{float:left;width:90%}}

.div_about section em{font-weight:bold;margin-right:1em}.div_about h3{margin-top:20px;margin-bottom:10px;font-size:1rem}.about_header{margin-top:0}@media (min-width: 391px){.about_header{margin-top:0.83em}}.section_timeline p{padding-left:50px;text-indent:-50px}.section_contact ul{margin-top:0;margin-bottom:10px}.section_contact li{font-size:0.9rem}a.contact_link{color:#848fa5}.adaptive-glass{--glass-lightness: 100%;background:hsl(0, 0%, var(--glass-lightness)/50%);-webkit-backdrop-filter:blur(40px);backdrop-filter:blur(40px)}@media (prefers-color-scheme: dark){.adaptive-glass{--glass-lightness: 0%}}@supports not ((-webkit-backdrop-filter: blur(1px)) or (backdrop-filter: blur(1px))){.adaptive-glass{background:hsl(0, 0%, var(--glass-lightness)/90%)}}

.ul_expi-list{display:flex;flex-wrap:nowrap;list-style:none;overflow-x:auto;padding:0}.ul_expi-list::-webkit-scrollbar-thumb{background:#888}.ul_expi-list::-webkit-scrollbar-thumb:hover{background:#555}.li_expi-list__item{white-space:nowrap}.btn_expi{cursor:pointer;border-style:none;border-bottom:2px solid #045388;background-color:transparent;color:#3498db;font-size:1rem;padding:0.6rem 0.8rem;color:grey}.btn_expi:focus{outline:none}.btn_expi:hover{background-color:#004857;color:#ffebc6;transition:background-color 300ms linear, color 300ms linear}.button--active{color:#ffebc6;border-bottom:2px solid #9ed6fbd8;background-color:#004857}

.div_experience>ul{text-align:justify;padding-left:1rem}.div_experience>ul>li{margin-top:1rem;font-size:0.9rem}@media (min-width: 768px){.div_experience>ul{max-width:550px}}

.div_main-nav{width:100%}.ul_main-nav__items{list-style:none;justify-content:space-around;padding:5px;border-top:2px solid #fafafa1a}@media (min-width: 391px){.ul_main-nav__items{border-top:0}}

.li_navigationItem{display:inline-block}.li_navigationItem>.active{background-color:#cd8e26}.li_navigationItem a{float:left;text-decoration:none;color:whitesmoke;font-weight:bold;padding:0.6rem 0.8rem}.li_navigationItem a:hover,.li_navigationItem a:active,.li_navigationItem a.active{color:#282c34}@media (min-width: 500px){.li_navigationItem{margin-top:1.5px;display:flex;height:100%;width:auto;align-items:center}.li_navigationItem a{color:#b6b5b5;height:100%;padding:16px 10px;border-bottom:4px solid transparent}.li_navigationItem a:hover{border-bottom:4px solid #cd8e26;color:whitesmoke;-webkit-transform:scale(1.1);transform:scale(1.1);transition:-webkit-transform 150ms ease-out;transition:transform 150ms ease-out;transition:transform 150ms ease-out, -webkit-transform 150ms ease-out}.li_navigationItem a:active,.li_navigationItem a.active{color:#282c34}}

.div_layout{position:relative;padding-left:15px;padding-right:15px;margin-right:auto;margin-left:auto;max-width:645px}.div_layout>.div_header{text-align:center}img{text-align:center;-webkit-filter:drop-shadow(3px 3px 3px #202020);filter:drop-shadow(3px 3px 3px #202020)}@media (min-width: 391px){.div_header{float:left}}.div_header h1{font-size:16px;margin:0.6em 0}.div_main{height:70vh;max-width:515px;background:transparent;border-radius:0.5ch;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);box-shadow:0 25px 50px rgba(0,0,0,0.2);padding:1rem}@media (min-width: 391px){.div_main{height:80vh;width:80%;position:absolute;left:10rem;float:left;padding:2rem}}.transition-enter,.transition-appear{opacity:0;z-index:1}.transition-enter-active,.transition-appear-active{opacity:1;transition:opacity 300ms linear 150ms}.transition-exit{opacity:1}.transition-exit.transition-exit-active{opacity:0;transition:opacity 300ms linear}.transition-exit-done{opacity:0}

